*{
    box-sizing: border-box;
}


body{
    margin:0;
    padding:0;
    height:100%;
    background-color: #f4f3f7;
    height:100%;
}

html,
body,
.container{
    height:100%;
    display: flex;
    justify-content: center;
}

.loader{
    width :100%;
    height:100%;
    display: flex; /*item 을 담는 container을 세로로 쌓는다는 뜻/ https://heropy.blog/2018/11/24/css-flexible-box/#:~:text=align%2Ditems%20%EB%8A%94%20Container%20%EB%82%B4,items%20%EC%86%8D%EC%84%B1%EB%B3%B4%EB%8B%A4%20%EC%9A%B0%EC%84%A0%ED%95%A9%EB%8B%88%EB%8B%A4..*/
    justify-content: center; /*flexible box) 레이아웃.서로 다른 크기의 화면과 기기에서도 HTML 요소들이 자동으로 재정렬되어, 웹 페이지의 레이아웃을 언제나 똑같이 유지할 수 있게 해줍니다.*/
    align-items:center; /* container안의 item이 한줄일 경우 사용*/
    font-weight: 300;
}

.movies{
    display:flex;
    justify-content:space-between;
    align-items: flex-start;
    flex-wrap: wrap ;/*Flex의 Items의 여러 줄 묶음(줄 바꿈) 설정*/
    padding:50px;/*테두리에서 얼마나 뗄건지*/
    padding-top:70px;
    width:80%;/*가로길이 지정*/
}
